.list_link {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
@media only screen and (max-width:992px) and (min-width: 480.5px) {
  .grid_item:nth-child(3n + 1) {
    grid-column: span 2 / span 2;
  }
}
@media only screen and (min-width: 992.5px) {
  .grid_item:nth-child(4n + 1),
  .grid_item:nth-child(4n + 4) {
    grid-column: span 2 / span 2;
  }
}


.list_back{
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
}

.list_item::-webkit-scrollbar {
  display: none;
}
.list_item {
  -ms-overflow-style: none;
  scrollbar-width: none; /* Firefox */
}